.redSpan{
    color:red;
    padding:3px;
}
p.dataNum{
    float: right;
    padding: 20px;
}
.int_right_bar {
    border: 1px solid #e0e6ed;
    margin-bottom: 20px;
    background-color: #fff;
    box-sizing: border-box;
}
.datalist{
    border-bottom: 1px solid #ededed;
    padding-bottom: 5px;
    padding-top: 10px;
}
.datalist img {
    width: 80px;
    height: 80px;
    vertical-align: center;
    border-radius: 8px;
    background: #f9f9f9;
    margin-top: 8px;
    padding: 10px;
}
.datalist h4 {
    font-size: 18px;
    color: #333;
}
.datalist p {
    font-size: 12px;
    color: #666;
    width: 611px;
    height: 17px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-bottom: 15px;
}

.green{
    border: 1px solid #49b9a1 !important;
    color: #49b9a1 !important;
    display: inline-block;
    -webkit-transform: scale(.8);
            transform: scale(.8);
}
.blue{
    border: 1px solid #4178e5 !important;
    color: #4178e5 !important;
    display: inline-block;
    -webkit-transform: scale(.8);
            transform: scale(.8);
}
.span_report {
    border: 1px solid #4178e5 !important;
    color: #4178e5 !important;
    display: inline-block;
    -webkit-transform: scale(.8);
            transform: scale(.8);
}
.datalist span {
    padding: 0px 3px;
    border: 1px solid #333;
    border-radius: 3px;
    margin-right: 5px;
}
.goodsprice {
    font-weight: 400;
    width: 100%;
    display: block;
    font-size: 17px;
    color: #f46067;
    margin-top: 20px;
    margin-bottom: 15px;
    text-align: right;
    padding-top: 15px;
}
.up_bg{
    display: block;
    text-align: center;
    background: url(/static/media/right_bar_supdemand_bg.1938cad0.png) no-repeat center center;
    background-size: 100%;
    height: 147px;
    box-sizing: border-box;
}
.int_right_bar .up_bg h4{
    color: #fff;
    font-size: 18px;
    text-align: center;
    background-color: transparent;
    padding: 20px 0 0 0;
    border: none;
    line-height: 30px;
    margin-bottom: 10px;
}
.down_icon{
    float: left;
    text-align: center;
    width: 33.333333333%;
    padding: 20px 0;
}
.down_icon img{
    height: 27px;
    margin-bottom: 10px;
    vertical-align: middle;
}
.down_icon>p {
    text-align: center;
    color: #333;
    font-size: 13px;
}

.int_right_bar ul li {
    font-size: 13px !important;
    color: #666;
    border-top: 1px solid #e0e6ed;
    width: 100%;
    padding: 15px 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.int_right_bar ul li a {
    color: #666;
    text-decoration: none;
}
.int_right_bar h4 {
    font-size: 16px;
    margin: 0;
    color: #333;
    padding: 8px 0 8px 52px;
    overflow: hidden;
    background-color: #f5f8fd;
    border-bottom: 1px solid #e0e6ed;
    position: relative;
}
.int_right_bar h4 img {
    position: absolute;
    top: 7px;
    left: 20px;
}
.int_right_bar h4 a {
    color: #999;
    font-size: 12px;
    float: right;
    line-height: 21px;
    margin-right: 6px;
}
.int_right_bar >a.twe_bar {
    text-decoration: none !important;
    background-size: 100%;
    display: block;
    height: 147px;
    background: url(/static/media/right_bar_twe_bg.ddc4cb38.png) no-repeat center center;
    color: #fff;
    font-size: 18px;
    text-align: center;
    padding: 35px 0px 0px 90px;
}

.apinew_head {
    border: 1px solid #efefef;
    margin-top: 20px;
    width: 100%;
    position: relative;
}
.apinew_head img {
    width: 180px;
    height: 180px;
    border-radius: 8px;
    background: #f9f9f9;
    margin-top: 20px;
    padding: 20px;
    vertical-align: center;
}
.api_lookthrough {
    background: url(data:image/png;base64,) no-repeat 0px;
    padding-left: 20px;
    font-size: 12px;
    color: #999;
    margin-top: 15px;
    float: left;
    margin-left: 10px;
}
.api_fenxiang {
    background: url(data:image/png;base64,) no-repeat 0px;
    padding-left: 20px;
    font-size: 12px;
    color: #999;
    margin-top: 15px;
    float: right;
    margin-right: 60px;
    cursor: pointer;
    font-weight: 400;
}
.apinew_head label {
    display: inline-block;
    max-width: 100%;
    margin-bottom: 5px;
    font-weight: 700;
}
.api_lookthrough span {
    border: none;
    font-weight: 400;
    padding: 2px 3px;
    border-radius: 3px;
    margin-right: 5px;
    font-size: 12px;
}
.apinew_head p {
    font-size: 14px;
    color: #333;
    line-height: 25px;
    margin-top: 10px;
}
.apinew_head h3 {
    font-size: 26px;
    color: #333;
    font-weight: 400;
}
.api_price_frame {
    padding: 15px;
    background: #f3f3f3;
    margin-bottom: 15px;
}
.api_price_frame p {
    margin-bottom: 0px;
    color: #fa8909;
}
.no-pad-l-r {
    padding-left: 0px;
    padding-right: 0px;
}
.api_format {
    width: 115px;
    height: 28px;
    font-size: 13px;
    float: left;
    text-align: center;
    line-height: 28px;
    margin-right: 10px;
    cursor: pointer;
    background: #fff;
    margin-bottom: 3px;
    color: #009fe2;
    border: 1px solid #009fe2;
}
.twe_entry {
    color: #999;
    font-size: 12px;
}
.apinew_head ul {
    width: 100%;
    float: left;
}
.apinew_head li {
    margin-right: 30px;
    float: left;
    line-height: 30px;
    color: #999;
}
.apinew_head li span {
    border: none;
    color: #333;
}
.datacontent_head span {
    padding: 2px 3px;
    border: 1px solid #333;
    border-radius: 3px;
    margin-right: 5px;
    font-size: 12px;
}
a.btn-primary{
    display: inline-block;
    text-align: center;
    width: 100%;
    height: 100%;
    background: linear-gradient(#337ab7,#2e6da4);
    border: none;
    width: 140px;
    cursor: pointer;
    height: 40px;
    border-radius: 0px;
    margin-top: 15px;
    line-height: 30px;
    color: #fff;
}
.data_title {
    position: static;
    top: 0;
    margin-top: 40px;
    border: 1px solid rgb(237, 237, 237);
    background: #f7f8fb;
    overflow: hidden;
    width: 100%;
    margin-bottom: 20px;
    z-index: 3;
}
.data_title div.data_title_active {
    background: #fff;
    color: #023465;
    border-top: 2px solid #0057b8;
}
.data_title div {
    float: left;
    padding: 15px 0px;
    cursor: pointer;
    border-right: 1px solid #efefef;
}
.data_title a {
    color: #333;
    text-decoration: none;
    padding: 15px 60px;
}
.title{
    margin-top: 20px;
    height: 60px;
    background-size: 15px;
    background: url(http://dfs1.tdata.cn/group1/M00/00/06/rBYERFkiWvmAfaMvAAABQVED50g411.png) no-repeat center bottom;
}
.title h4{
    font-size: 26px;
    font-weight: 400;
    text-align: center;
}
.supplier_intro p{
    text-indent: 2em;
    color: #666;
    line-height: 25px;
    margin-bottom: 0px;
    margin-top: 5px;
    margin-bottom: 5px;
    padding-top: 5px;
    padding-left: 20px;
    padding-right: 20px;
}

